<template>
  <div>
    <div class="clearfix top-filter">
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleAdd"
      >新增</el-button>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="180"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <edit-dialog v-model="dialogVisible" :dialog-form="dialogForm" :edit-type="dialogType" :title="dialogTitle" @submit="init" />
  </div>
</template>
<script>
import { getListApi, getDeleteApi } from '@/apis/modules/base/baseTransferType'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
export default {
  components: {
    EditDialog
  },
  mixins: [paginationPage],
  data() {
    return {
      query: {
        costCenterName: ''
      },
      dialogForm: {},
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      costCenterPullList: [],
      dialogType: 'add',
      dialogVisible: false,
      columnList: [
        {
          module: 'word',
          prop: 'type',
          label: '移库类型',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'description',
          label: '移库描述',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'explanation',
          label: '移库说明',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources_dictText',
          label: '数据来源',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增${this.title}`
      } else {
        return `编辑${this.title}`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 新增
    handleAdd() {
      this.dialogType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.dialogType = 'modufy'
      this.dialogVisible = true
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该${this.title}, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        getDeleteApi({
          id: item.id,
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

