import request from '@/apis/request.wms'

// 列表
export function getListApi(data) {
  return request({
    url: '/basic/wmsMoveStorageType/pageList',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data) {
  return request({
    url: '/basic/wmsMoveStorageType/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data) {
  return request({
    url: '/basic/wmsMoveStorageType/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/basic/wmsMoveStorageType/delete',
    method: 'post',
    data
  })
}

